import React, { Component, Suspense } from "react";
import FileSaver from "file-saver";
import { Nav, Tab } from 'react-bootstrap';
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import { Uppy } from "@uppy/core";
//import FileInput from "@uppy/react/lib/FileInput";
import Transloadit from "@uppy/transloadit";
const GetRightsGenralRequest = React.lazy(() => import('./GetRightsSteps/GetRightsGenralRequest'));
const GetRightsSocialAccount = React.lazy(() => import('./GetRightsSteps/GetRightsSocialAccount'));
const GetRightsCopyUrl = React.lazy(() => import('./GetRightsSteps/GetRightsCopyUrl'));
const GetRightsPending = React.lazy(() => import('./GetRightsStatus/GetRightsPending'));
const GetRightsDownload = React.lazy(() => import('./GetRightsStatus/GetRightsDownload'));
class AssetsModalAside extends Component {
    state = {
        step: 1,
        brandLogo: null,
        comment: 'What a fantastic shot! We loved your post. Would you mind us utilizing it for our brand?',
        tncName: "",
        tncUrl: "",
        tnc: false
    }

    onClickToChange = (step) => event => this.setState({ step })

    componentWillMount() {
        this.uppy = new Uppy({ debug: true, autoProceed: true });

        this.uppy.use(Transloadit, {
            params: {
                auth: {
                    key: "key"
                },
                template_id: "templateId"
            },
            waitForEncoding: true
        });
    }

    componentWillUnmount() {
        this.uppy.close();
    }
    termsFileDownload = () => {
        //var blob = new Blob(["Hello, world!"], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(baseUrl('media/downloads/Terms&ConditionsforuseofUGC.docx'), "Terms&ConditionsforuseofUGC.docx");
    }
    modalClose = () => {
        this.props.modalClose(false)
    }
    render() {
        const { modalAssetData } = this.props
        const { step } = this.state
        console.log("modalAssetData", modalAssetData)
        return modalAssetData && Object.keys(modalAssetData).length > 0 ? <div className="rights_modal_sidebar position-relative">
            <div className="rights_modal_aside_wrap">
                {modalAssetData.ugcRights == null ? <Tab.Container defaultActiveKey="ugc-genralRequest">
                    <div className="ugc-wizard-nav">
                        <Nav variant="nav" className="bg-light justify-content-between row gx-0">
                            <Nav.Item className="col-4">
                                <Nav.Link className="py-3 py-xl-4 block-center active" eventKey="ugc-genralRequest">
                                    <span className="svg-icon text-success">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-.997-6l7.07-7.071-1.414-1.414-5.656 5.657-2.829-2.829-1.414 1.414L11.003 16z" fill="currentColor" />
                                        </svg>
                                    </span>
                                    <span className="fw-bold text-nowrap">General Request</span>
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="col-4">
                                <Nav.Link className="py-3 py-xl-4 block-center" eventKey="ugc-socialAccount">
                                    <span className="svg-icon">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path className="checkIco" d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm0-2a8 8 0 1 0 0-16 8 8 0 0 0 0 16zm-.997-4L6.76 11.757l1.414-1.414 2.829 2.829 5.656-5.657 1.415 1.414L11.003 16z" fill="currentColor" />
                                            <path className="checkIcoFilled" d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-.997-6l7.07-7.071-1.414-1.414-5.656 5.657-2.829-2.829-1.414 1.414L11.003 16z" fill="currentColor" />
                                        </svg>
                                    </span>
                                    <span className="fw-bold text-nowrap">Social Account</span>
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item className="col-4">
                                <Nav.Link className="py-3 py-xl-4 block-center" eventKey="ugc-copyUrl">
                                    <span className="svg-icon">
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                            <path fill="none" d="M0 0h24v24H0z" />
                                            <path className="checkIco" d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm0-2a8 8 0 1 0 0-16 8 8 0 0 0 0 16zm-.997-4L6.76 11.757l1.414-1.414 2.829 2.829 5.656-5.657 1.415 1.414L11.003 16z" fill="currentColor" />
                                            <path className="checkIcoFilled" d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-.997-6l7.07-7.071-1.414-1.414-5.656 5.657-2.829-2.829-1.414 1.414L11.003 16z" fill="currentColor" />
                                        </svg>
                                    </span>
                                    <span className="fw-bold text-nowrap">Copy URL</span>
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </div>
                    <Tab.Content>
                        <Tab.Pane eventKey="ugc-genralRequest">
                            <Suspense fallback={<div>Loading...</div>}>
                                <GetRightsGenralRequest state={this.state} />
                            </Suspense>
                        </Tab.Pane>
                        <Tab.Pane eventKey="ugc-socialAccount">
                            <Suspense fallback={<div>Loading...</div>}>
                                <GetRightsSocialAccount />
                            </Suspense>
                        </Tab.Pane>
                        <Tab.Pane eventKey="ugc-copyUrl">
                            <Suspense fallback={<div>Loading...</div>}>
                                <GetRightsCopyUrl />
                            </Suspense>
                        </Tab.Pane>
                    </Tab.Content>
                </Tab.Container> : modalAssetData.ugcRights == 1 ? <Suspense fallback={<div>Loading...</div>}>
                    <GetRightsPending />
                </Suspense> : modalAssetData.ugcRights == 1 ?
                    <Suspense fallback={<div>Loading...</div>}>
                        <GetRightsDownload />
                    </Suspense> : null}
            </div>
            {modalAssetData.ugcRights == null ? <div className="rights_modal_actions text-center modal-footer w-100">
                {step == 1 || step == 2 ? <><button type="button" className="btn btn-secondary me-2" onClick={this.modalClose}>Cancel</button>
                    <button type="button" className="btn btn-primary" onClick={this.onClickToChange(step == 1 ? 2 : step == 2 ? 3 : 4)}>Next Step</button>
                </> : null}

                {step == 4 ? <button type="button" className="btn btn-secondary me-2" onClick={this.modalClose}>Done</button> : null}
            </div> : null}
        </div> : null
    }
}

export default AssetsModalAside;